import React from 'react'
import {
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  Input, Stack,
    Select
} from '@chakra-ui/react'
import RouterDrawer from './RouterDrawer'
import Form from './Form'
import FormField from './FormField'
import FormImageField from './FormImageField'
import FormSubmitButton from './FormSubmitButton'
import {useHistory} from 'react-router-dom'
import {Role} from '../domain/Person'
import useStore from '../hooks/useStore'

interface AddPersonDrawerProps {
  name: string
}

const AddPersonDrawer = () => {
  const history = useHistory()
  const {personStore, organizationStore} = useStore()

  const handleSubmit = async (values: any) => {
    await personStore.create({
      ...values,
      organizationId: organizationStore.currentOrganizationId
    })
    history.push('?')
  }

  return (
      <RouterDrawer name="add-person">
        <Form onSubmit={handleSubmit} defaultValues={{ role: Role.USER }}>
          <DrawerOverlay>
            <DrawerContent>
              <DrawerCloseButton/>
              <DrawerHeader>Add member</DrawerHeader>

              <DrawerBody>
                <Stack spacing={4}>
                  <FormField name="name" label="Name" required>
                    <Input autoFocus />
                  </FormField>

                  <FormField name="user.email" label="Email">
                    <Input type="email" />
                  </FormField>

                  <FormField name="position" label="Position">
                    <Input />
                  </FormField>

                  <FormField name="role" label="Role">
                    <Select defaultValue={Role.USER}>
                      <option value={Role.ADMIN}>Admin</option>
                      <option value={Role.MANAGER}>Manager</option>
                      <option value={Role.USER}>User</option>
                    </Select>
                  </FormField>

                  <FormImageField label="Image" name="image" />
                </Stack>
              </DrawerBody>

              <DrawerFooter>
                <FormSubmitButton>Add</FormSubmitButton>
              </DrawerFooter>
            </DrawerContent>
          </DrawerOverlay>
        </Form>
      </RouterDrawer>
  )
}

export default AddPersonDrawer