import React from 'react'
import {Input, SimpleGrid, Stack, Avatar, FormLabel} from '@chakra-ui/react'
import Form from './Form'
import FormField from './FormField'
import Card from '../ui/Card'
import Organization from '../domain/Organization'
import FormSubmitButton from './FormSubmitButton'
import FormImageField from './FormImageField'

export interface OrganizationEditFormProps {
  onSubmit: (data: any) => void
  organization: Organization
}

const OrganizationEditForm: React.FC<OrganizationEditFormProps> = ({onSubmit, organization}) => {
  return (
      <Form onSubmit={onSubmit} defaultValues={organization} successMessage="Organization saved successfully!">
        <Card>
          <Stack spacing={4}>
            <FormField name="name" required label="Organization name">
              <Input/>
            </FormField>
            <FormImageField label="Logo" name="logo" />
            <div>
              <FormSubmitButton>
                Save
              </FormSubmitButton>
            </div>
          </Stack>
        </Card>
      </Form>
  )
}

export default OrganizationEditForm